@import url('https://fonts.googleapis.com/css2?family=Nunito&display=swap');

* {
	margin: 0;
	padding: 0;
	box-sizing: border-box;
}

html {
	font-family: 'Nunito', sans-serif;
	font-size: 22px;
	color: rgb(0, 0, 0);
	background: repeating-linear-gradient( 20deg, transparent 0 8px, #eee 9px 9px, transparent 10px 23px, #eee 24px 24px), repeating-linear-gradient( 65deg, transparent 0 8px, #eee 9px 9px, transparent 10px 23px, #eee 24px 24px), repeating-linear-gradient(110deg, transparent 0 8px, #eee 9px 9px, transparent 10px 23px, #eee 24px 24px), repeating-linear-gradient(155deg, transparent 0 8px, #eee 9px 9px, transparent 10px 23px, #eee 24px 24px);
	min-height: 100vh;
}
body{
	width: 100%;
	min-height: 100vh;
	padding: 0;
	margin: 0;
	word-wrap: break-word;
	hyphens: auto;
	background: repeating-linear-gradient( 20deg, transparent 0 8px, #eee 9px 9px, transparent 10px 23px, #eee 24px 24px), repeating-linear-gradient( 65deg, transparent 0 8px, #eee 9px 9px, transparent 10px 23px, #eee 24px 24px), repeating-linear-gradient(110deg, transparent 0 8px, #eee 9px 9px, transparent 10px 23px, #eee 24px 24px), repeating-linear-gradient(155deg, transparent 0 8px, #eee 9px 9px, transparent 10px 23px, #eee 24px 24px);

}


#BackgroundBody {
	
	min-height: 100vh;
	background-color: rgb(247, 247, 247);
	/*  .App*/
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: center;
	min-height: 100vh;
	padding: 1rem 0.5rem;
	color: #fff;
	font-size: 22px;
}

#BackgroundBody > section {
	width: 100%;
	max-width: 420px;
	min-height: 400px;
	display: flex;
	flex-direction: column;
	justify-content: flex-start;
	padding: 1rem;
	background-color: rgba(0, 0, 0, 0.4);
}

#BackgroundBody form {
	display: flex;
	flex-direction: column;
	justify-content: space-evenly;
	flex-grow: 1;
	padding-bottom: 1rem;
}

#BackgroundBody a,
#BackgroundBody a:visited {
	color: #fff;
}

#BackgroundBody input[type='text'],
input[type='password'],
button,
textarea {
	font-family: 'Nunito', sans-serif;
	font-size: 22px;
	padding: 0.25rem;
	border-radius: 0.5rem;
	color: #000;
}

#BackgroundBody button,
label {
	margin-top: 1rem;
}

#BackgroundBody > button {
	padding: 0.5rem;
}

.instructions {
	font-size: 0.75rem;
	border-radius: 0.5rem;
	background: #000;
	color: #fff;
	padding: 0.25rem;
	position: relative;
	bottom: -10px;
}

.instructions svg {
	margin-right: 0.25rem;
}

.offscreen {
	position: absolute;
	left: -9999px;
}

.hide {
	display: none;
}

.valid {
	color: limegreen;
	margin-left: 0.25rem;
}

.invalid {
	color: red;
	margin-left: 0.25rem;
}

.errmsg {
	background-color: lightpink;
	color: firebrick;
	font-weight: bold;
	padding: 0.5rem;
	margin-bottom: 0.5rem;
}

.line {
	display: inline-block;
}

#title-singreg {
	margin-top: 20px;
	margin-bottom: 10px;
	font-size:36px;
  }

#button-sing{
	margin-top: 20px;
	margin-bottom: 10px;
	padding: 7px;
}